.page {
  width: 100%;
  height: 100%;
  position: absolute;
}

.top-tags {
  bottom: auto;
  top: 35px;
}

.tags {
  color: #ffd700;
  opacity: 0.6;
  position: absolute;
  bottom: 0;
  left: 100px;
  font-size: 18px;
  font-family: 'Barlow';
}

.bottom-tag-html {
  margin-left: -20px;
}

.container {
  width: 100%;
  will-change: contents;
  height: 90%;
  min-height: 566px;
  opacity: 0;
  position: absolute;
  top: 5%;
  margin: 0 auto;
  z-index: 1;
  transform-style: preserve-3d;
  animation: fadeIn 1s forwards;
  animation-delay: 1s;

  @media screen and (max-width: 767.98px) {
    height: 100%;
  }
}

/* ABOUT - CONTACT */
.about-page,
.contact-page {
  .text-zone {
    position: absolute;
    left: 10%;
    top: 50%;
    transform: translateY(-50%);
    vertical-align: middle;
    display: table-cell;
    width: 35%;
    max-height: 90%;
    @media screen and (max-width: 768px) {
      left: 33%;
      top: 38%;
      width: 54%;
    }

    h1 {
      font-size: 53px;
      color: #ffd700;
      font-family: 'Barlow';
      font-weight: 400;
      position: relative;
      margin-top: 0;
      margin-bottom: 40px;
      left: 10px;

      @media screen and (max-width: 767.98px) {
        font-size: 20px;
        letter-spacing: 2px;
      }
      @media screen and (min-width: 768px) and (max-width: 1200px) {
        font-size: 44px;
        letter-spacing: 2px;
      }

      &::before {
        content: '<h1>';
        font-family: 'Moon Dance';
        font-size: 18px;
        position: absolute;
        margin-top: -10px;
        left: -10px;
        opacity: 0.6;
        line-height: 18px;
      }
      &::after {
        content: '<h1/>';
        font-family: 'Moon Dance';
        font-size: 18px;
        line-height: 18px;
        position: absolute;
        left: -30px;
        bottom: -20px;
        margin-left: 20px;
        opacity: 0.6;
      }
    }

    p {
      font-size: 13px;
      color: #ffffff8f;
      font-family: sans-serif;
      font-weight: 300;
      min-width: fit-content;
      animation: pulse 1s;
      @media screen and (max-width: 768px) {
        font-size: 10px;
      }

      &:nth-of-type(1) {
        animation-delay: 1.1s;
      }

      &:nth-of-type(2) {
        animation-delay: 1.2s;
      }

      &:nth-of-type(3) {
        animation-delay: 1.3s;
      }
    }
  }

  .text-animate-hover {
    &:hover {
      color: #fff;
    }
  }
}
